e;\n}\n\n.hide {\n    display: none;\n    height: 70px;\n    width: 500px;\n    overflow-y: auto;\n    border-radius: 5px;\n}\n\n.hide::-webkit-scrollbar {\n    border-radius: 5px;\n}\n\n.task-name:hover > .hide {\n    display: block;\n    position: absolute;\n    background-color: var(--font-color);\n    color: var(--theme-col-3);\n    border: none;\n    outline: none;\n    padding: 12px;\n    top: -70px;\n    left: 27px;\n}\n\n.lists > div {\n    position: relative;\n}\n\n.lists > div > button {\n    display: none;\n    background-color: var(--theme-col-2);\n    border: none;\n    outline: none;\n}\n\n.lists > div:hover > button {\n    background-color: var(--sidebar-hover);\n    display: block;\n    position: absolute;\n    right: 10px;\n    bottom: 10px;\n}\n\n.list > div > button {\n    background-color: var(--theme-col-2);\n    border: none;\n}\n\n.lists > div > button > img {\n    filter: invert(98%) sepia(4%) saturate(464%) hue-rotate(212deg)\n        brightness(115%) contrast(87%);\n}\n\n.modal {\n    position: fixed;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    height: 200px;\n    width: 320px;\n    outline: none;\n    border: none;\n    background-color: var(--font-color);\n    border-radius: 20px;\n}\n\n.modal::backdrop {\n    backdrop-filter: blur(10px);\n    background-color: rgba(0, 0, 0, 0.5);\n}\n\n.modal form {\n    display: grid;\n    justify-content: center;\n    gap: 20px;\n    color: var(--theme-col-3);\n}\n\n.modal form > * {\n    display: grid;\n    justify-content: center;\n    gap: 8px;\n}\n\n.modal form > .buttons {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    gap: 15px !important;\n}\n\n.modal form h3,\n.modal label,\n.modal button {\n    font-family: \"Open Sans\", sans-serif;\n    font-weight: 500;\n    font-style: normal;\n}\n\n.modal input {\n    font-family: \"Open Sans\", sans-serif;\n    font-optical-sizing: auto;\n    font-weight: 400;\n    font-style: normal;\n    padding: 5px;\n    